import Req from '@/api/http.js'


const API_EXP = {
  /* 课堂报告接口
   * @param  {object} params
   * @config  {object} params header等
   * @return {promise}
   */
  getReport(params, config) {
    let { roomId, userId } = params
    // return Req.get(`${HTTP_SERVER}/api/interactive/v1/room/1342687597285081088/report/user/1179`, params, config)
    // return Req.get(`${HTTP_SERVER}/mock/56/api/interactive/v1/room/:roomId/report/user/:userId`, params, config)
    return Req.get(`/service/api/interactive/v1/room/${roomId}/report/user/${userId}`, {}, config)
  },
  getSchoolInfo (params, config) {
    return Req.post(
      // `${HTTP_SERVER_LMS}/service/api/upms/index/info`, params, config
      `/service/api/cas/index/info`, params, config
    )
  },
}

export default API_EXP
