/**
 * axios 封装
 * @author  xiezhiwen
 * @date 2020-03-14
 */

import axios from 'axios'
const envHost = require('@/common/envHost.js')
// 本地开发或者本地测试环境
const eh = envHost.getEnvHost()
const HTTP_SERVER = eh.server

let language = ''
language = 'zh_CN'

// 给请求统一加上参数(带上token)
function addParams (params = {}) {
  return params
}

function getQueryVariable (variable) {
  var query = window.location.search.substring(1);
  var vars = query.split('&')
  for (var i = 0; i < vars.length; i++) {
    var pair = vars[i].split('=')
    if (pair[0] == variable) { return pair[1] }
  }
  return (false)
}

let org = /^192/.test(window.location.host) ? location.origin.replace(/\d.*?:/, 'localhost:') : location.origin
let headers = {
  'content-type': 'application/jsoncharset=UTF-8',
  'X-Api-Client-Terminal': 'web',
  'X-Api-Client-Terminal-Version': 0,
  'X-Api-Client-Language': language,
  'X-Api-Client-Origin': org,
  'X-Api-Client-System': 'lms',
  'X-Api-Client-Uid': getQueryVariable('userId'),
  'X-Api-Client-Unit-id': getQueryVariable('unitId') || ''
}
const clientArr = location.href.match(/saas\/([\d]+)/)
if (clientArr && clientArr.length > 1) {
  headers['X-Api-Client-Unit-Id'] = clientArr[1]
}

const Axios = axios.create({
  baseURL: HTTP_SERVER,
  timeout: 15000,
  headers: headers
})

Axios.interceptors.request.use(
  config => {
    return config
  },
  err => {
    console.log(err)
    return Promise.reject(null)
  }
)

Axios.interceptors.response.use(
  response => {
    console.log(response)
    const { data = {} } = response
    return data
  },
  error => {
    console.log(error, JSON.stringify(error))
    const { code, message } = error
    if (code === 'ECONNABORTED' || message === 'Network Error') {
      // 请求超时
      return Promise.resolve({
        success: false,
        state: {
          code: -8,
          msg: '请求出错，请稍后再试！'
        }
      })
    } else {
      if (message !== 'cancel-request') {
        return Promise.resolve({
          state: {
            code: -9,
            msg: '请求出错，请稍后再试！'
          }
        })
      } else {
        return Promise.resolve({
          state: {
            code: -10,
            // msg: '重复请求自动取消！'
            msg: ''
          }
        })
      }
    }
  }
)

const ajax = {}

ajax.get = (url, params = {}, myConfig = {}) => {
  return Axios.get(url, {
    params: addParams(params),
    ...myConfig
  })
}
ajax.post = (url, params = {}, myConfig = {}) => {
  return Axios(url, {
    method: 'post',
    data: params,
    ...myConfig
  })
}

export default ajax
