//全局mixins
import reportReq from '@/api/report';
const envHost = require('@/common/envHost.js')
const eh = envHost.getEnvHost()

export const globalMixins = {
  components: {},
  data() {
    return {
      // LibIcon,
      loadingObj: {}, //loading 对象
      successCode: 2000000, // 服务器返回成功code
      envHost: eh
    };
  },
  computed: {
    userinfo() {
      return this.$store.getters.userinfo;
    },
    appLang() {
      return this.$store.state.lang;
    },
    clientHeight() {
      return this.$store.getters.clientHeight;
    },
  },
  created() {
  },
  mounted() { },
  methods: {
    //所有请求前置、过滤器
    reqFilter(reqName, args) {
      let { params, configs, fn, hideToast} = args
      let method = '';
      let userExp = reqName.match(new RegExp(/^user\.([a-zA-Z0-9]*)/)); // 用户api
      let teachReachExp = reqName.match(new RegExp(/^teachReach\.([a-zA-Z0-9]*)/)); // 学习中心api
      if (userExp && userExp.length > 1) {
        method = reportReq[userExp[1]];
      } else if (teachReachExp && teachReachExp.length > 1) {
        // method = teachReachReq[teachReachExp[1]];
      }
      try {
        return method(params, configs).then(r => {
          if (fn && typeof fn === 'function') {
            fn(r);
          } else {
            const { data, state } = r
            if (/^2+/.test(state.code)) {
              return typeof data === 'boolean' ? data : (data || {})
            } else {
              if (!configs || configs && !configs.selfMessage) {
                if (state.code != -10) { // -10取消的重复请求
                  !hideToast && this.$toast({ type: 'fail', message: r.state.msg })
                }
              }
              return Promise.reject(r.state)
            }
          }
        })
      } catch (e) {
        return Promise.reject(e)
      }
    },
    checkPhotoUrl (photoUrl) {
      if (photoUrl.startsWith('http') || photoUrl.startsWith('https')) {
        return photoUrl
      } else {
        return `${this.envHost.server}/${photoUrl}`
      }
    }
  }
}
