import Vue from 'vue'
import VueRouter from 'vue-router'
import CRM from '../views/CRM/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ClassReport',
    component: CRM
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
