<template>
  <div class="class-report">
    <van-nav-bar title="3iClass Q&A" left-arrow @click-left="back" />
    <div v-if="showNav">
      <van-tree-select height="100vh" @click-nav="clickNav" @click-item="clickItem" :items="items" :main-active-index.sync="active" />
    </div>
    <div v-if="!showNav" style="width: 100%">
      <van-panel :title="title">
        <div style="margin: 2vh 4vh; font-size: 14px">原因： {{ reason }}</div>
        <div style="margin: 2vh 4vh; font-size: 14px; color: grey">
          请尝试以下指引操作
        </div>
        <div style="margin: auto 4vh; font-size: 14px; word-break: break-all" v-html="content"></div>
        <div v-if="imageSrc" style="text-align: center">
          <van-image :src="`https://matthew2015.github.io/3iClassQ-A${imageSrc}`" />
        </div>
      </van-panel>
      <div style="margin: 2vh 4vh; font-size: 14px; color: grey">
        如问题未能解决，请联系客服同事处理
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import config from "./config";
export default {
  name: "CRM",
  components: {},
  data () {
    return {
      showNav: true, // 是否显示导航
      content: "",
      title: "",
      reason: "",
      imageSrc: "",
      active: 0,
      items: [],
    };
  },
  mounted() {
    this.items = config.items
  },
  methods: {
    back () {
      this.showNav = true;
    },
    clickNav (index) {
      this.active = index;
    },
    clickItem (data) {
      this.showNav = false;
      console.log(config.items[this.active].children[data.id - 1])
      this.title = config.items[this.active].children[data.id - 1].text || "";
      this.reason =
        config.items[this.active].children[data.id - 1].reason || "/";
      this.content =
        config.items[this.active].children[data.id - 1].content || "";
      this.imageSrc =
        config.items[this.active].children[data.id - 1].imageSrc || "";
    },
  },
};
</script>

