const API_SERVER = require('@/config/server-config')
const DefaultSetting = require('@/config/default-setting')

let _ = {
  server: '',
  getEnvHost () {
    // 生产环境请求地址
    console.log('location.hostname', location.hostname)
    console.log('API_SERVER', API_SERVER)
    console.log('DefaultSetting.packType', DefaultSetting.packType)
    if (DefaultSetting.packType === 'production') {
      if (location.hostname.startsWith('qa')) {
        _.server = API_SERVER.SEVER_QA
      } else if (location.hostname.startsWith('dev')) {
        _.server = API_SERVER.LOCAL
      } else if (location.hostname.startsWith('pre')) {
        _.server = API_SERVER.SEVER_PV
      } else {
        _.server = API_SERVER.SEVER
      }
    } else {
      if (location.hostname.startsWith('localhost')) {
        _.server = ''
      }
    }
    console.log('-', _)
    return _
  }
}

module.exports = _